body {
  background: #eee;
}

.loading {
  margin-top: 80px;
}

.tab-pane {
  background: white;
  padding: 10px;
  border-left: 1px solid #ddd;
}

.input-with-button {
  position: relative;
  padding: 10px;
}

.input-with-button input {
  padding-right: 80px;
}

.input-with-button .btn {
  position: absolute;
  top: 10px;
  right: 10px;
}

.modal {
  display: block;
}

.connection-form {
  margin-top: 80px;
  background: white;
  border-radius: 10px;
  border: 1px solid #ccc;
}

#console-output, #chat-output {
  max-height: 480px;
  overflow-y: auto;
}

#console-output-inner, #chat-output-inner {
  padding: 5px 10px;
  min-height: 480px;
}

#console-output-inner div, #chat-output-inner div {
  padding: 10px 0 0;
  border-bottom: 1px solid #eee;
}

#console-output-inner p.text-muted,
#chat-output-inner p.text-muted {
  margin-bottom: 5px;
}

.chat-name {
  color: red;
}

.chat-name small {
  margin-left: 10px;
  color: #777;
}

.console-prefix {
  color: orange;
  font-weight: 600;
}

.modal {
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.4);
  width: 100%;
  height: 100%;
}

.modal-inner {
  position: relative;
  left: 50%;
  top: 50%;
  margin-left: -250px;
  margin-top: -300px;
  background: white;
  padding: 10px;
  height: auto;
  width: 500px;
  border: 2px solid #aaa;
}

.modal-title {
  margin: 10px auto 20px;
  text-align: center;
}

.modal-close {
  position: absolute;
  top: 25px;
  right: 25px;
  cursor: pointer;
}

td {
  padding: 10px;
}

td .progress {
  margin: 0;
}

td .btn {
  margin-left: 10px;
}

.progress-wrapper {
  display: inline-block;
  margin: 0 20px;
}

.dashboard-progress {
  width: 200px;
  height: 200px;
}

.progressbar-text {
  font-size: 30px;
}

h1 {
  margin: 10px 0 30px;
}

.modal textarea {
  height: 200px;
}



